.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  color: #494949;
}

html, body, .container-fluid {
  width: 100%;
  height: 100%;
  margin: auto;
}

.scroll-disappear {
  opacity: 0 !important;
  transition: 1s;
}

.portfolio-scroll-disappear {
  opacity: 0 !important;
  transition: .5s;
}
