#header-text {
    position: absolute;
    top: 22%;
    width: 100%;
    margin: auto;
    font-family: 'Lobster', cursive;
    color: #eb774c;
    transition: .5s;
}

#header-text h1 {
    width: 50%;
    padding-right: 3%;
}

.header-message {
    text-align: center;
    margin-left: 1vw;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

.header-sub-message {
    width: 50%;
