#content-1-shape {
    width: 50vw;
    height: 50vw;
    margin: auto;
    background: #eaba4d;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    position: absolute;
    overflow: hidden;
    z-index: 0;
    top: -95%;
    left: -35%;
    transition: .5s;
}

.content-1-shape-hover {
    -webkit-transform: rotate(45deg) !important;
    transform: rotate(45deg) !important;
    top: -103% !important;
    left: -42% !important;
    transition: .5s !important;
}

#content-1-diamond  {
    width: 20vw;
    height: 20vw;
    margin: auto;
    background: #1c3750;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    border-radius: 50%;
    position: absolute;
    top: 15%;
    left: 5%;
    transition: .5s;
}

#content-diamond-1-text {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    width: 100%;
    margin-top: 35%;
    margin-left: 4%;
    color: white;
    transition: .5s;

}

.body-content {
    position: relative;
    overflow: hidden;
    height: 55%;
    display: block;
    width: 100%;
}

#content-1-diamond:hover, #content-2-diamond:hover, #content-3-diamond:hover {
    transition: .5s;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    border-radius: 40px;
    text-decoration: none;
    
}

#content-1-diamond:hover #content-diamond-1-text {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    transition: .5s;
    margin-left: 1vw;
    margin-top: 16vh;
}

#content-1-text {
